import React from "react";
import ReactDOM from "react-dom/client"
import { Link } from "react-router-dom";


const Navbar=()=>{
    return <div className="navbar">
     <div className="logo-name">
     <Link> <img className="logo" src="data:image/png;base64,"></img></Link>
      <p><span className="other">Other</span> Tirupathi, Andhara Pradhesh, India <i className="bi bi-chevron-down"></i></p>
     </div>
     <div className="items">
      <ul>
        <Link to="/about"><li><i class="bi bi-info-circle"></i> About</li></Link>
        <Link to="/offer"><li><i className="bi bi-gift"></i> Offer</li></Link>
        <Link to="/help"> <li><i className="bi bi-question-circle"></i> Help</li></Link>
        <li><i className="bi bi-person-circle"></i> Sign in</li>
        <li><i className="bi bi-cart2"></i> Cart</li>
      </ul>
     </div>
    </div>
  }
  export default Navbar;
  